import Launch from '../imgs/launch.jpg';
import Shop from '../imgs/shop.jpg';
import Spell from '../imgs/spell.jpg';
const contractAddress = process.env.CONTRACT_ADDRESS;

const Intro = () => {
  return (
    <>
    <section className="uk-container uk-section uk-padding-remove-bottom">
      <div className="uk-padding uk-padding-remove-horizontal uk-width-3-5@s uk-margin-auto">
        <p className="uk-dropcap uk-text-lead">
          Ancient runes and symbols are believed to hold magical properties that has healed and guided our ancestors since the beginning of time. 
          Runes NFT project brings this magic to you in randomly generated images featuring unique artwork of stone themed backgrounds and artifacts.
        </p>
        <h3 id="roadmap" className="uk-heading-small uk-text-center uk-padding uk-padding-remove-bottom">Project Roadmap</h3>
        <div className="uk-margin-medium-top">
          <ul className="uk-flex-center" uk-switcher="animation: uk-animation-fade; toggle: > *" uk-tab="true">
            <li className="uk-width-auto uk-width-1-4@m"><a>Phase 1</a></li>
            <li className="uk-width-auto uk-width-1-4@m"><a>Phase 2</a></li>
            <li className="uk-width-auto uk-width-1-4@m"><a>Phase 3</a></li>
          </ul>
          <ul className="uk-switcher uk-margin uk-text-center">
            <li>
              <div className="uk-card uk-card-secondary">
                <div className="uk-card-media-top">
                  <img src={Launch} alt="Rune NFT Launch" width="100%" />
                </div>
                <div className="uk-card-header">
                  <h2 className="uk-text-heading-small">Genesis Collection</h2>
                </div>
                <div className="uk-card-body">
                  <p>Our Genesis collection offers 777 unique and randomly generated Rune tokens to be minted. Owners of Rune tokens can trade them on Opensea or collect them for Phase 2.</p>
                </div>
              </div>
            </li>
            <li>
              <div className="uk-card uk-card-secondary">
                <div className="uk-card-media-top">
                  <img src={Shop} alt="Rune NFT Merchandise" width="100%" />
                </div>
                <div className="uk-card-header">
                  <h2 className="uk-text-heading-small">Exclusive Rune Merchandise</h2>
                </div>
                <div className="uk-card-body">
                  <p>Once all 777 tokens have been minted and unlocked owners will be granted exclusive access to order custom merchandise with their Rune NFT.</p>
                </div>
              </div>
            </li>
            <li>
              <div className="uk-card uk-card-secondary">
                <div className="uk-card-media-top">
                  <img src={Spell} alt="Rune NFT Spellcasting" width="100%" />
                </div>
                <div className="uk-card-header">
                  <h2 className="uk-text-heading-small">Spell Casting</h2>
                </div>
                <div className="uk-card-body">
                  <p>Owners that hold more than one Rune will be awarded an opportunity to cast a spell which will reward them with an NFT from our future Exodus collection.</p>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </section>
    <p className="uk-text-muted uk-text-small uk-text-center uk-text-truncate uk-margin-remove">Contract address: {contractAddress}</p>
    </>
  );
};

export default Intro;