import { useEffect, useState } from 'react';
import { ethers } from 'ethers';
import RunesNFT from '../artifacts/contracts/RunesNFT.sol/Rune.json';
import Intro from './Intro'
import NFTImage from './NFTImage';


function Sale() {
    const contractAddress = import.meta.env.VITE_CONTRACT_ADDRESS;
    const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
    const signer = provider.getSigner();
    const contract = new ethers.Contract(contractAddress, RunesNFT.abi, signer);
    const address = signer.getAddress();
    
    const [totalMinted, setTotalMinted] = useState(0);
    useEffect(() => {
      getCount();
    }, []);
  
    const getCount = async () => {
      await contract.totalSupply()
      .then(a => setTotalMinted(parseInt(a)));
    };

    const [isWhitelist, setIsWhitelist] = useState(0);
    useEffect(async () => {
        await contract.isAllowListActive()
        .then(a => setIsWhitelist(a));
    }, []);

    const [mintsAllowed, setMintsAllowed] = useState(0);
    useEffect(async () => {
      await contract.numAvailableToMint(address)
      .then(a => setMintsAllowed(a));
    }, []);
    
    return (
        <>
        <Intro />
        <h2 className="uk-heading-medium uk-text-center">Mint Your Rune NFT!</h2>
        {!isWhitelist ? ('') : (
            <div className="uk-margin-medium-top">
            <p className="uk-text-center uk-text-lead">Minting is now open for whitelisted members.</p>
            <p className='uk-text-center'>You are allowed {mintsAllowed} mint(s).</p>
            </div>
        )}
        <div className="uk-margin-medium">
            <div className="uk-grid-small uk-flex uk-flex-center uk-text-center" uk-grid="true">
              <NFTImage tokenId={totalMinted + 1} getCount={getCount} whiteList={isWhitelist} />
            </div>
        </div>
        <section className="uk-container uk-section uk-padding-remove-bottom">
          <h2 className="uk-heading-medium uk-text-center">View Collection</h2>
          <p className="uk-text-center">Visit <a href={import.meta.env.VITE_CHAIN_ID == 80001 ? (
              'https://testnets.opensea.io/collection/runes-nft'
            ) : (
              'https://opensea.io/collection/runes-nft'
            )} rel="noopener noreferrer nofollow" target="_blank" >Opensea</a> to view the collection of tokens already minted.</p>
        </section>
        </>
    );
}

export default Sale;