import { ethers } from 'ethers';
import { useEffect, useState } from 'react';
import RunesNFT from '../artifacts/contracts/RunesNFT.sol/Rune.json';
import Intro from './Intro'
import Whitelist from './Whitelist';


function Closed() {
  const contractAddress = process.env.CONTRACT_ADDRESS;
  const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
  const signer = provider.getSigner();
  const contract = new ethers.Contract(contractAddress, RunesNFT.abi, signer);
  
  useEffect(() => {
    window.ethereum.on('chainChanged', () => {
        window.location.reload();
    });
    window.ethereum.on('accountsChanged', () => {
        window.location.reload();
    });
  });

  const [spots, setSpots] = useState(0);
  useEffect(() => {
    async function getSpots() {
      const wl = await contract.whiteListLength();
      setSpots(77-wl)
    }
    getSpots();
  }, []);

  return (
    <>
      <Intro />
      <h2 id="mint" className="uk-heading-medium uk-text-center">Rune NFT Sale is Coming!</h2>
      <div className="uk-margin-medium-top">
        <p className="uk-text-center uk-text-lead">Our pre-launch event will take place on June 21, 2022 at 05:14 EDT marking the arrival of the Summer Solstice.</p>
        <p className="uk-text-center">Whitelisted wallet holders are guaranteed an opportunity to mint the first 77 Rune NFT's from the start of our pre-launch until 6:00 am EDT, then our public sale will commence.</p>
        {spots > 0 ? (
          <>
            <p className="uk-text-center">Join the whitelist to guarantee your spot and we will reserve a mint for you at a discounted price.</p>
            <p className='uk-text-center uk-text-lead'>Hurry! There is only a total of {spots} spots available, don't miss your opportunity.</p>
            <Whitelist />
          </>
        ) : (
          <p className='uk-text-center uk-text-lead'>Sorry! Our whitelist is full. Make sure you come back for our public sale at 6:00 am EDT on the Summer Solstice.</p>
        )}
        <section className="uk-padding-large uk-padding-remove-horizontal uk-padding-remove-bottom">
          <div className="uk-grid-small uk-child-width-auto uk-flex-center" uk-grid="true" uk-countdown="date: 2022-06-21T05:14:00-05:00">
            <div>
                <div className="uk-countdown-number uk-countdown-days"></div>
                <div className="uk-countdown-label uk-margin-small uk-text-center uk-visible@s">Days</div>
            </div>
            <div className="uk-countdown-separator">:</div>
            <div>
                <div className="uk-countdown-number uk-countdown-hours"></div>
                <div className="uk-countdown-label uk-margin-small uk-text-center uk-visible@s">Hours</div>
            </div>
            <div className="uk-countdown-separator">:</div>
            <div>
                <div className="uk-countdown-number uk-countdown-minutes"></div>
                <div className="uk-countdown-label uk-margin-small uk-text-center uk-visible@s">Minutes</div>
            </div>
            <div className="uk-countdown-separator">:</div>
            <div>
                <div className="uk-countdown-number uk-countdown-seconds"></div>
                <div className="uk-countdown-label uk-margin-small uk-text-center uk-visible@s">Seconds</div>
            </div>
          </div>
          <p className="uk-text-center uk-text-lead">To Pre-Launch</p>
        </section>
      </div>
    </>
  );
}

export default Closed;