import { useEffect, useState } from 'react';
import { ethers } from 'ethers';
import UIkit from 'uikit';
import Icons from 'uikit/dist/js/uikit-icons';
import Intro from '../components/Intro'
import Install from '../components/Install';
import Home from '../components/Home';
import RunesNFT from '../artifacts/contracts/RunesNFT.sol/Rune.json';

// loads the Icon plugin
UIkit.use(Icons);

const CID = {
  1: 'Mainnet',
  3: 'Ropsten',
  4: 'Rinkeby',
  5: 'Goerli',
  10: 'Optimism',
  42: 'Kovan',
  56: 'BSC',
  137: 'MATIC',
  31337: 'Hardhat',
  42161: 'Arbitrum One',
  43114: 'Avalanche',
  80001: 'Mumbai',
}

function App() {
  const isTestnet = () => {
    if (process.env.CHAIN_ID != 1) {
      return (
        <div className='uk-alert-danger' uk-sticky="bottom: true" uk-alert="true">
          <a className="uk-alert-close" uk-close="true"></a>
          <p className='uk-text-center'>Test Environment. Connected to {CID[process.env.CHAIN_ID]} network. For testing purposes only. Report issues <a href="https://github.com/0xrune/0xrune.github.io/issues" rel="noopener noreferrer nofollow" target="_blank">here</a>.</p>
        </div>
      );
    } 
  }

  if (window.ethereum === undefined) {
    return (
      <>
        {isTestnet()}
        <Install />
      </>
    );
  }
  
  const contractAddress = process.env.CONTRACT_ADDRESS;
  const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
  const signer = provider.getSigner();
  const contract = new ethers.Contract(contractAddress, RunesNFT.abi, signer);
  const [account, setAccount] = useState(0);
  const [cid, setCid] = useState(0);
  const [cname, setCname] = useState('');

  async function connectWallet() {
    document.getElementById('cnctBtn').setAttribute('disabled', 'true');
    const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
    await provider.send('eth_requestAccounts', [])
      .then(a => setAccount(a[0]))
      .catch(e => {
        alert("Error: " + e.message);
        document.getElementById('cnctBtn').removeAttribute('disabled');
      });
    const b = await provider.getNetwork();
    setCid(b.chainId);
    const c = await contract.name();
    setCname(parseInt(c));
    console.log(a);
  }

  if (account === 0) {
    return (
        <>
          {isTestnet()}
          <Intro />
          <h2 id="mint" className="uk-heading-medium uk-text-center">Connect your wallet!</h2>
          <div className="uk-margin-medium-top">
            <p className="uk-text-center uk-text-lead">You must connect your wallet to continue. Please make sure you have logged in to your wallet and it is connected to this site, then refresh the page.</p>
          </div>
          <div className="uk-text-center" uk-lightbox="true">
            <button id="cnctBtn" className="uk-button uk-button-primary uk-button-large" onClick={connectWallet}>Connect Wallet</button>
          </div>
        </>
    )
  }

  if (cid != process.env.CHAIN_ID) {
    if (cid == 0) {
      return (
        <>
          {isTestnet()}
          <Intro />
          <h2 id="mint" className="uk-heading-medium uk-text-center">Network not found!</h2>
          <div className="uk-margin-medium-top">
            <p className="uk-text-center uk-text-lead">Unable to connect to {CID[process.env.CHAIN_ID]}.</p>
          </div>
        </>
      );
    } else {
      return (
        <>
          {isTestnet()}
          <Intro />
          <h2 id="mint" className="uk-heading-medium uk-text-center">Incorrect Network!</h2>
          <div className="uk-margin-medium-top">
            <p className="uk-text-center">Please open your wallet and switch to the {CID[process.env.CHAIN_ID]} network to continue.</p>
          </div>
        </>
      );
    }
  }
  
  if (cname === "") {
    return (
      <>
        {isTestnet()}
        <Intro />
        <h2 id="mint" className="uk-heading-medium uk-text-center">Contract not found!</h2>
        <div className="uk-margin-medium-top">
          <p className="uk-text-center uk-text-lead">The smart contract has not yet been deployed to the {CID[process.env.CHAIN_ID]} network.</p>
        </div>
      </>
    )
  }

  return (
    <>
      {isTestnet()}
      <Home />
    </>
  );
}

export default App;