import Intro from './Intro'

const Install = () => {
    return (
      <>
        <Intro />
        <h2 id="mint" className="uk-heading uk-text-center">Install Meta Mask!</h2>
        <div className="uk-flex uk-flex-center">
          <div className='uk-width-auto uk-text-center'>
            <a href="https://metamask.io/download.html" 
              rel="noopener noreferrer nofollow" 
              target="_blank" 
              className="uk-button uk-button-danger uk-button-large">Install Meta Mask
            </a>
            <p className='uk-text-lead'>You must connect your wallet to mint a Rune NFT.</p>
          </div>
        </div>
      </>
    );
  };
  
export default Install;