import { useEffect, useState } from 'react';
import { ethers } from 'ethers';
import RunesNFT from '../artifacts/contracts/RunesNFT.sol/Rune.json';
import Intro from './Intro'
import Sale from './Sale';
import Closed from './Closed';


function Home() {
  const contractAddress = process.env.CONTRACT_ADDRESS;
  const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
  const signer = provider.getSigner();
  const contract = new ethers.Contract(contractAddress, RunesNFT.abi, signer);

  const [isActive, setIsActive] = useState(false);
  useEffect(() => {
    async function getStatus() {
      const s = await contract.status()
      setIsActive(s);
    }
    getStatus();
  }, []);
  
  const [totalMinted, setTotalMinted] = useState(0);
  useEffect(() => {
    async function getSupply() {
      const t = await contract.totalSupply()
      setTotalMinted(parseInt(t));
    }
    getSupply();
  }, []);

  if (totalMinted < 777 && isActive === true) {
    return (
      <>
        <Sale />
      </>
    );
  } else if (isActive === false) {
    return (
      <>
        <Closed />
      </>
    );
  } else {
    return (
      <>
        <Intro />
        <h2 id="mint" className="uk-heading-medium uk-text-center">All 777 Rune are minted!</h2>
        <div className="uk-margin-medium-top">
          <p className="uk-text-center uk-text-lead">All Rune NFT's have been minted.</p>
        </div>
      </>
    );
  }
}

export default Home;
