import React from 'react';
import { useEffect, useState } from 'react';
import { ethers } from 'ethers';
import RunesNFT from '../artifacts/contracts/RunesNFT.sol/Rune.json';
import placeholder from '../imgs/placeholder.png';


function NFTImage({ tokenId, getCount, whiteList }) {
	const contractAddress = process.env.CONTRACT_ADDRESS;
	const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
	const signer = provider.getSigner();
	const contract = new ethers.Contract(contractAddress, RunesNFT.abi, signer);

	const [isMinted, setIsMinted] = useState(false);
	useEffect(() => {
		getMintedStatus();
	}, []);

	const getMintedStatus = async () => {
		const result = await contract.isContentOwned(tokenId, '.json');
		setIsMinted(result);
	};

	const mintToken = async () => {
		if (whiteList === true) {
			const minted = await contract.mint(tokenId, '.json', {
				value: ethers.utils.parseEther('0.05'),
			}).then(res => res.wait())
				.then(getMintedStatus())
				.then(getCount())
				.catch(err => alert("Sorry, we are unable to mint your token at this time.\n" + err.data.message));
			if (minted) {
				window.location.reload();
			}
		} else {
			const minted = await contract.mint(tokenId, '.json', {
				value: ethers.utils.parseEther('0.06'),
			}).then(res => res.wait())
				.then(getMintedStatus())
				.then(getCount())
				.catch(err => alert("Sorry, we are unable to mint your token at this time.\n" + err.data.message));
			if (minted) {
					window.location.reload();
			}
		}
	};

	return (
		<div className={!isMinted ? ("uk-flex-first") : ("uk-flex-none")}>
			<div className="uk-card uk-card-default uk-card-body uk-margin-auto uk-dark">
				<div>
					<img src={placeholder} width="128" height="128" alt={'Rune'.concat(' #', String(tokenId).padStart(3, '0'))} />
				</div>
				<button id={tokenId} className="uk-button uk-button-primary" onClick={mintToken}>Mint</button>
			</div>
		</div>
	);
}

export default NFTImage;